import {
  fetchClientFunction
} from "@/utils/interface";
import {
  Toast
} from 'vant'
import {
  setStore,
  getStore,
  clearStore
} from '@/utils/storage'

//重新登录 
export const loadLogin = () => {
  clearStore("accessToken")
  Toast.fail('登录过期，请重新登录')
  const data = {
    type: "login",
    params: '',
  };
  fetchClientFunction(data);
}
// 刷新token 
export const loadToken = () => {
  let data = {
    type: "token",
    params: "",
  };
  fetchClientFunction(data);
  console.log("fetchtoken end");
}
// 判断是否有token 
export const checkToken = () => {
  // loadToken()
  // console.log(getStore('accessToken'),"checkToken")
  // if (!getStore('accessToken')) {
  //   loadLogin()
  //   return false
  // }
  return true
}



