import api from './base/index'
import {
  get,
  post
} from '@/utils/request'

export const fetchList = (parameter, type) => {
  //默认设置pageSize
  // let pageSize = 10
  // if(!!parameter.pageSize){
  //     Object.assign(parameter, pageSize); 
  // }
  switch (type) {
    case "getTextBookList":
      return get(api.getTextBookList, parameter)
    default:
      return true
  }
}



