import url_base from '@/utils/base'

const {
  BASE_URL,
  information
} = url_base

const api = {
  BASE_URL,  

  // exam
  getNotes: `${BASE_URL}/exam/notes/getNotes`, //获取考试须知


  // textbook
  getTextBookList:  `${BASE_URL}/textbook/getList`, //获取教材



  
  detailPolicy: (type)=>{
     if (type == 'owner') return `${BASE_URL}/owner-policy-detail/`
     return `${BASE_URL}/wenzheng/detail/`
  } , //问政详情


  /**用户信息 */
  fetchUserInfo: `${BASE_URL}/user/my`,
  refreshToken: `${BASE_URL}/user/token/refresh`,
  fetchLevel: `${BASE_URL}/user/my-level`, // 我的等级


  /**文件上传 */
  uploadFile: `${information}/image/upload`, // 上传图片 post






}

export default api



