import axios from 'axios'
import {
  Toast
} from 'vant'
import {
  setStore,
  getStore,
  clearStore
} from '@/utils/storage'
import {
  loadLogin,
  loadToken
} from '@/api/login'
import router from '@/router/basic'

import qs from 'qs'


let tokenFlag = true
// 创建axios实例
const service = axios.create({
  // baseURL: resourceUrl, // api的base_url
  timeout: 30000 // 请求超时时间
})

// request拦截器
service.interceptors.request.use(config => {
  if (getStore("accessToken")) {
    config.headers['Authorization'] = getStore("accessToken") // 让每个请求携带自定义token 请根据实际情况自行修改
  }
  // config.headers['Accept'] = "application/vnd.zhengqihao.v1+json";
  // config.headers['X-Requested-With'] = "XMLHttpRequest";
  config.headers['Content-Type'] = config.headers['Content-Type'] ? config.headers['Content-Type'] : 'application/x-www-form-urlencoded;charset=UTF-8';
  //todo
  // config.headers['X-Client-HashID'] = getStore("browserId");
  // config.headers['X-Client-HashID'] = "2vx0p9256e7ydjwz"
  // config.headers['X-Client-HashID'] = "96lx83zx7o4ykw5m" //
  // config.headers['X-CA-NONCE'] = getStore("ca_nonce"); //防止重复评论

  // console.log(config,"config")
  if (config.method == 'post') {
    config.headers['Content-Type'] = "application/x-www-form-urlencoded"
    // config.headers['Cache-Control'] =  "no-cache, private"
    // config.headers['Accept'] = "application/json"
  }
 
  return config
}, error => {
  // Do something with request error
  // console.log(error, "error config") // for debug
  Promise.reject(error)
})

const toLogin = () => {  
  // put(api.refreshToken).then(res => {
  //   console.log(res.data, "toLogin")
  //   if (!!res.data) {
  //     const tokenData = "Bearer " + res.data.auth.token
  //     setStore("accessToken", tokenData);
  //   } else {
  //     tokenFlag = false
  //     loadLogin()
  //   }
  // }).catch(err => {
  //   console.log(err, "toLogin err")
  //   tokenFlag = false
  //   loadLogin()
  // })
  loadLogin()
}

const errorHandle = (status, errdata) => {
  // console.log(status,errdata,"errorHandle error 111111111111") // debug
  const {
    config,
    data
  } = errdata
  if(config.method == 'post' || config.method == 'patch') {
    if (!!data.errmsg) {
      Toast.fail(data.errmsg);
    }
  } 

  if (data.errcode == 10001 && data.errmsg) Toast.fail(data.errmsg)
  switch (status) {
    case 401:
      // if (!!getStore("accessToken")) {
      //   toLogin()
      //   return
      // }
      loadLogin()
      // if (!getStore("accessToken")) {
      //   loadLogin()
      //   return
      // }
      break;
    case 403:
      break;
    case 404:
      // router.push({name: "404Entry"})
      Toast.fail('请求的资源不存在')
      break;
  }

  // return Promise.reject(data)
}

// respone拦截器
service.interceptors.response.use(
  response => {
    // console.log(response,"response")
    // let res = response.data
    // res.status === 200 ? Promise.resolve(response) : Promise.reject(response)
    if (response.status == 200) { // 成功处理
      console.log(response,response.data.message, "responseCode")
      if (response.data.success) {
        return response.data
      } else {
        Toast(response.data.message)
        return false
      }
    } else { // 错误处理/
      Toast.fail('服务器开小差了~稍后重试')
      return Promise.reject('error')
    }
  },
  error => {
    // console.log('err' + error) // for debug
    const {
      response
    } = error
    if (response) {
      errorHandle(response.status, response)
      return Promise.reject(response)
    }
  }
)

export default service

/**
 * 向url后追加参数 ?name=value&name1=value1
 * 后台只需要用 GetMapping 注解即可，参数上不需要加注解
 * */

export function get(url, data) {
  return service({
    url: url,
    method: 'get',
    params: data
  })
}

/**
 * 向url后追加参数 ?name=value&name1=value1
 * 后台只需要用 GetMapping 注解即可，参数上不需要加注解
 * */

export function patch(url, data) {
  return service({
    url: url,
    method: 'patch',
    params: data
  })
}

export function put(url, data) {
  return service({
    url: url,
    method: 'put',
    params: data
  })
}

export function deleteApi(url, data) {
  console.log("------delete")
  return service({
    url: url,
    method: 'delete',
    data: data
  })
}

/**
 * 表单参数是在请求体中，也是name=value&name1=value1的形式在请求体中，
 * 后台只需要用 PostMapping 注解即可，参数上不需要加注解
 * @param url  字符串类型
 * @param data  json类型
 * */
export function post(url, data) {
  console.log("------post")
  return service({
    url: url,
    method: 'post',

    data: data,
    transformRequest: [function (data) {
      data = qs.stringify(data, {
        allowDots: true
      }) // , {arrayFormat: 'brackets'}
      return data
    }]
  })
}

/**
 * 表单参数是在请求体中，也是name=value&name1=value1的形式在请求体中，
 * 后台只需要用 PostMapping 注解即可，参数上不需要加注解
 * @param url  字符串类型
 * @param data  json类型
 * */
export function uploadUrl(url, data) {
  console.log("------uploadUrl")
  return service({
    url: url,
    method: 'post',
    headers: {
      'Content-Type': 'multipart/form-data'
    },
    data: data
  })
}
/**
 * 参数以json格式传给后， {name:'li',code:'001'}
 * 1. 后台需要用 PostMapping 注解
 * 2. 后台必须是一个参数
 * 3. 参数上必须增加 RequestBody 注解
 * @param url  字符串类型
 * @param data  json类型
 * */
export function postJSON(url, data) {
  return service({
    url: url,
    method: 'post',
    headers: {
      'Content-Type': 'application/json;charset=UTF-8',
      Accept: 'application/json;charset=UTF-8'
    },
    data: data
  })
}



