<template>
  <div id="VantList">
    <van-list v-model="loading" 
    :finished="finished" 
    :finished-text="finishedText" 
    @load="onLoad"
      :immediate-check="isImmediate">
      <slot name='prevList'></slot>
      <slot name='nowList' :list='list'></slot>
      <slot name='nextList'></slot>
    </van-list>
  </div>
</template>

<script>
import { fetchList } from "@/api/list";

export default {
  name: "VantList",
  props: {
    options: {
      type: Object,
      default: {},
    },
  },
  data() {
    return {
      finalOption: {
        pageSize: 10
      },
      loading: true,
      finished: true,
      finishedText: "我是有底线的",
      isImmediate: false,
      list: [],
      pageNo: 0,
    };
  },

  mounted() {
    let finalOption = this.finalOption;
    Object.assign(finalOption, this.options);
    this.finalOption = finalOption;
    console.log(this.finalOption)
    this.refresh()
  },

  methods: {
    onLoad() {
      // 异步更新数据
      setTimeout(() => {
        this.loadList();
      }, 100);
    },

    // 刷新列表
    refresh() {
      this.list = [];
      this.pageNo = 0;
      console.log(this.list, this.pageNo, this.active, "refresh");
      this.loadList();
    },

    async loadList() {
      const type = this.active;
      let box_barr = [];
      this.pageNo++;
      let res = await fetchList({ ...this.finalOption,pageNo: this.pageNo }, this.finalOption.apiType);
      const targetArr = res.result.data;
      if (Array.isArray(targetArr)) {
        box_barr = this.list.concat(targetArr);
        this.list = box_barr;
      }
      const { totalPage, pageNo } = res.result;
      // 数据全部加载完成
      this.finished = !!(pageNo >= totalPage);
      // 加载状态结束
      this.loading = false;
    },
  },
};
</script>
<style lang="scss" src='./index.scss' scoped></style>



